<template>
  <div class="recommend-wrap" ref="recommendWrap">
    <div class="list-box">
      <section class="list-title">
        <h2 class="name">今日歌单</h2>
        <a class="mui_tit__more" href="javascript:;" @click="handleClick('/recommend-song-list', '今日歌单')">更多<img src="~@/assets/rightgreyrow.png"/></a>
      </section>
      <ul class="scroll-box">
        <li class="item" v-for="(item, index) in filterdDiscList" :key="index" @click="selectItem(item)">
          <img class="avatar" width="60" height="60" v-lazy="item.imgurl" alt="">
          <div class="text">
            <h3 class="name" v-html="item.creator.name"></h3>
            <p class="desc" v-html="item.dissname"></p>
          </div>
        </li>
      </ul>
    </div>
    <div class="list-box">
      <section class="list-title">
        <h2 class="name">今日歌曲</h2>
        <a class="mui_tit__more" href="javascript:;" @click="handleClick('/recommend-song-list', '今日歌曲')">更多<img src="~@/assets/rightgreyrow.png"/></a>
      </section>
      <ul class="song">
        <li class="item" @click="selectItem(item, index)" v-for="(item, index) in songList" :key="index">
          <div class="rank">
            <span :class="getRankCls(index)">{{getRankText(index)}}</span>
          </div>
          <div class="info">
            <h2 class="name">{{item.songname}}</h2>
            <p class="desc">{{item.singername}}</p>
          </div>
        </li>
      </ul>
    </div>
    <div class="list-box lastone">
      <section class="list-title">
        <h2 class="name">今日MV</h2>
        <a class="mui_tit__more" href="javascript:;" @click="handleClick('/recommend-song-list', '今日MV')">更多<img src="~@/assets/rightgreyrow.png"/></a>
      </section>
      <ul class="mv-box">
        <li class="item" v-for="(item, index) in mvList" :key="index" @click="playVideo(item)">
          <div class="mv-bg" v-lazy:background-image="item.cover">
            <i class="iconfont icon-bofang play"></i>
          </div>
          <div class="name">{{item.name}}</div>
        </li>
      </ul>
    </div>
    <router-view></router-view>
    <loading v-show="!discList.length"></loading>
  </div>
</template>

<script>
  import {getDiscList} from '../../api/recommend'
  import {getTopList} from '../../api/rank'
  import {ERR_OK} from '../../api/config'
  import {playlistMixin} from '../../common/js/mixin'
  import mvJson from './data/mv.json'
  import {mapMutations} from 'vuex'
  import video from '@/base/video'

  export default {
    mixins: [playlistMixin],
    data() {
      return {
        songList: [],
        discList: [],
        mvList: mvJson
      }
    },
    created() {
      this._getDiscList()
      this._getTopList()
    },
    computed: {
      filterdDiscList() {
        return this.discList.slice(0, 6)
      }
    },
    methods: {
      handleClick(url, title) {
        this.$router.push({path: url, query: {title}})
      },
      handlePlaylist(playlist) {
        let bottom = playlist.length > 0 ? '60px' : ''
        this.$refs.recommendWrap.style.bottom = bottom
      },
      selectItem(item) {
        this.$router.push({path: `/disc`, query: {id: item.dissid}})
        this.setDisc(item)
      },
      _getDiscList() {
        getDiscList().then((res) => {
          if (res.code == ERR_OK) {
            this.discList = res.data.list
          }
        })
      },
      _getTopList() {
        getTopList().then((res) => {
          if (res.code == ERR_OK) {
            this.songList = res.data.topList[0].songList
          }
        })
      },
      getRankCls(index) {
        return index < 3 ? `icon icon${index}` : 'text'
      },
      getRankText(index) {
        return index < 3 ? '' : index + 1
      },
      playVideo(item) {
        video({
          videoURL: item.videoUrl
        })
      },
      ...mapMutations({
        setDisc: 'SET_DISC'
      })
    }
  }
</script>

<style scoped lang="scss">
  @import "../../common/scss/variable";
  @import "../../common/scss/mixin";
  .recommend-wrap {
    position: absolute;
    left: 0;
    top: 88px;
    right: 0;
    bottom: 0;
    background: #f5f5f5;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
      .list-box {
        overflow: hidden;
        &.lastone {
          padding-bottom: 80px;
          .mv-box {
            margin: 0 20px;
            .item {
              margin-bottom: 30px;
              .mv-bg {
                padding-bottom: 55.6%;
                background-size: cover;
                background-position: center center;
                position: relative;
                .play {
                  position: absolute;
                  left: 50%;
                  top: 50%;
                  transform: translate(-50%, -50%);
                  font-size: 48px;
                  color: #f2ebeb;
                }
              }
              .name {
                font-size: 14px;
                line-height: 1.3;
                overflow:hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                margin-top: 6px;
              }
            }
          }
        }
        .list-title {
          height: 30px;
          line-height: 30px;
          margin: 22px 20px 11px;
          position: relative;
          .name {
            font-weight: 700;
            font-size: 17px;
            color: $color-theme;
          }
          .mui_tit__more {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            font-size: 13px;
            display: flex;
            align-items: center;
            color: #999;
            img {
              width: 8px;
              margin-top: -3px;
              margin-left: 3px;
            }
          }
        }
        .scroll-box {
          display: flex;
          overflow-x: scroll;
        }
        .item {
          // display: flex;
          // align-items: center;
          // padding: 0 20px 20px;
          .avatar {
            flex: 0 0 60px;
            width: 120px;
            height: 120px;
            border-radius: 16px;
            margin-right: 20px;
          }
          .text {
            display: flex;
            flex-direction: column;
            justify-content: center;
            flex: 1;
            line-height: 20px;
            font-size: $font-size-medium;
            word-break: break-all;
            padding-right: 20px;
            .name {
              margin-top: 10px;
              color: $color-text;
            }
            .desc {
              color: $color-text-d;
            }
          }
          &:first-child {
            .avatar {
              margin-left: 20px;
            }
            .text {
              padding-left: 20px;
            }
          }
        }
      }
  }
  .song {
    margin: 0 20px;
    background: #333;
    border-radius: 4px;
    .item {
      display: flex;
      align-items: center;
      height: 64px;
      font-size: $font-size-medium;
      padding-left: 10px;
      &:not(:last-child) {
        border-bottom: 1px solid #363636;
      }
      .rank {
        display: flex;
        align-items: center;
        flex: 0 0 25px;
        width: 25px;
        text-align: center;
        margin-right: 30px;
        .icon {
          width: 25px;
          height: 25px;
          background-size: contain;
          &.icon0 {
            @include bg("../../base/song-list/first");
          }
          &.icon1 {
            @include bg("../../base/song-list/second");
          }
          &.icon2 {
            @include bg("../../base/song-list/third");
          }
        }
        .text {
          width: 25px;
          height: 25px;
          line-height: 25px;
          font-size: $font-size-large;
          color: $color-theme;
        }
      }
      .info {
        flex: 1;
        line-height: 20px;
        overflow: hidden;
        .name {
          @include dot;
          color: #f5f5f5;
        }
        .desc {
          @include dot;
          margin-top: 4px;
          color: $color-text-d;
        }
      }
    }
  }
</style>



