<template>
<div class="video-wrap" v-show="videoURL" @touchmove.prevent style="zIndex: 1000">

    <div class="content-wrap">

        <div class="video-size">
            <p class="close" @click="close"><i class="icon icon-dismiss"></i></p>
            <video ref="video" class="video"
                    autoplay
                    webkit-playsinline
                    playsinline
					controlslist="nodownload"
                    :src="videoURL" controls="controls"></video>
        </div>

    </div>

</div>
</template>
<script>
export default {
	data () {
		return {
			videoURL: ''
		}
	},
	methods: {
		close () {
			this.videoURL = ''
			if (this.$refs.video) {
				this.$refs.video.pause()
			}
		}
	}
}
</script>
<style lang="scss" scoped>
.video-wrap{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: rgb(0,0,0);
    .content-wrap{
        width: 100%;
        .video-size{
            position: relative;
            width: 100%;
            .video{
				width: 100%;
				max-height: 70vh;
                object-fit: fill;
            }
        }
        .close{
            position: absolute;
            right: 0;
            top: -38px;
            .icon-dismiss{
                font-size: 36px;
                color: #fff;
            }
        }
    }

}

</style>



