import Vue from 'vue'
import video from './index.vue'

let Video = Vue.extend(video)
let instance = null
let appEl = null

export default (option = {}) => {
	let videoURL = option.videoURL || ''
	appEl = appEl || document.getElementById('app')
	if (!instance) {
		instance = new Video()
		document.body.insertBefore(instance.$mount().$el, appEl)
	}
	if (videoURL) {
		instance && Object.assign(instance, {
			videoURL: videoURL
		})
	}
}



